import { Avatar, Button, Checkbox, Divider, FormControlLabel, List, ListItem, ListItemAvatar, ListItemText, TextField, Typography } from '@mui/material';
import React from 'react';
import { getNameInfo, IBasicNameInfo } from '../../services/nameService';
import SearchContainer from '../searchContainer/SearchContainer';
import BarChartIcon from '@mui/icons-material/BarChart';
import PercentIcon from '@mui/icons-material/Percent';
import MilitaryTechIcon from '@mui/icons-material/MilitaryTech';
import StarsIcon from '@mui/icons-material/Stars';
import SwapHorizIcon from '@mui/icons-material/SwapHoriz';
import BadgeIcon from '@mui/icons-material/Badge';
import ReportProblemIcon from '@mui/icons-material/ReportProblem';

interface INameInputProps {
	name: String,
	setName(newName: string): void;
}

function NameInput(props: INameInputProps) {
	return (
		<TextField
			label="Digite um nome"
			value={props.name}
			fullWidth
			onChange={e => props.setName(e.target.value)}
		/>
	)
}

function NameNotFound() {
	return (
		<div className="NameNotFound">
			<List
				sx={{
					width: '100%',
					maxWidth: 360,
					backgroundColor: 'white',
					borderRadius: '25px',
					border: 'solid 1px red'
				}}
			>
				<ListItem>
					<ListItemAvatar>
						<Avatar>
							<ReportProblemIcon />
						</Avatar>
					</ListItemAvatar>
					<ListItemText primary="Nome no encontrado"/>
				</ListItem>
			</List>
		</div>
	)
}

function BasicNameData(props: IBasicNameInfo) {
	return (
		<div className="BasicNameData">
			<List
				sx={{
					width: '100%',
					maxWidth: 360,
					backgroundColor: 'white',
					borderRadius: '25px',
					border: 'solid 1px green'
				}}
			>
				<ListItem>
					<ListItemAvatar>
						<Avatar>
							<BadgeIcon />
						</Avatar>
					</ListItemAvatar>
					<ListItemText primary="Nome encontrado" secondary={props.nome} />
				</ListItem>
				<Divider />
				<ListItem>
					<ListItemAvatar>
						<Avatar>
							<BarChartIcon />
						</Avatar>
					</ListItemAvatar>
					<ListItemText primary="Frequncia no Brasil" secondary={props.freq + ' Pessoas'} />
				</ListItem>
				<ListItem>
					<ListItemAvatar>
						<Avatar>
							<PercentIcon />
						</Avatar>
					</ListItemAvatar>
					<ListItemText primary="Porcentagem no Brasil" secondary={props.percentual.toFixed(2) + '%'} />
				</ListItem>
				<ListItem>
					<ListItemAvatar>
						<Avatar>
							<MilitaryTechIcon />
						</Avatar>
					</ListItemAvatar>
					<ListItemText primary="Ranking no Brasil" secondary={props.rank + ''} />
				</ListItem>
				<ListItem>
					<ListItemAvatar>
						<Avatar>
							<StarsIcon />
						</Avatar>
					</ListItemAvatar>
					<ListItemText primary="Estado com maior ndice" secondary={`${props.ufMax} (${Number.parseFloat(props.ufMaxProp).toFixed(2)} a cada 100.000 habitantes)`} />
				</ListItem>
				<ListItem>
					<ListItemAvatar>
						<Avatar>
							<SwapHorizIcon />
						</Avatar>
					</ListItemAvatar>
					<ListItemText primary="Nomes similares includos na estatstica" secondary={props.nomes.replaceAll(",", ", ")} />
				</ListItem>
			</List>
		</div>
	)
}

function SearchPage() {

	const [name, setName] = React.useState<String>("")
	const [basicNameData, setBasicNameData] = React.useState<IBasicNameInfo | null | undefined>(null);

	const [useBasicData, setUseBasicData] = React.useState<boolean>(true);

	function handleSearch(): void {
		if (useBasicData) {
			getNameInfo(name).then(nameInfo => {
				console.log(nameInfo.length)
				setBasicNameData(nameInfo.length > 0 ? nameInfo[0] : undefined);
			})
		}

	}

	return (
		<div className="App">
			<SearchContainer>
				<Typography
					component={"h1"}
					style={{
						fontSize: '64px',
						color: 'black',
						textAlign: 'center'
					}}>
					Buscar nome no IBGE
				</Typography>
				<NameInput name={name} setName={setName} />
				<FormControlLabel
					value={useBasicData}
					checked={useBasicData}
					onChange={(e, checked) => setUseBasicData(checked)}
					control={<Checkbox />}
					label="Dados bsicos"
					labelPlacement="start"
					disabled
				/>
				<Button
					disabled={name.length < 3}
					onClick={handleSearch}
					variant="outlined"
				>
					{name.length < 3 ? "Digite pelo menos trs letras para buscar!" : "Buscar"}
				</Button>
				{basicNameData === null ? <></> : (
					basicNameData === undefined ? <NameNotFound /> : 
					<BasicNameData {...basicNameData} />
				)
				}
			</SearchContainer>
		</div>
	);
}

export default SearchPage;